"use client";
import { api } from "@/app/_components/lib/api";
import Image from "next/image";
import { useParams, useRouter } from "next/navigation";
import { useEffect, useState } from "react";
import { motion } from "framer-motion";
import { Eye, CheckCircle, ArrowLeft } from "lucide-react";

interface OrganisasiPageProps {
  name: string
  short_description: string
  long_description: string
  id: number
  logo: string
  visi: string
  misi: string
  is_organization: boolean
}

export default function OrganisasiDetailPage() {
  const { id } = useParams();
  const router = useRouter();
  const [organisasiData, setOrganisasiData] = useState<OrganisasiPageProps | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);

  useEffect(() => {
    const fetchOrganisasiData = async () => {
      try {
        console.log("Fetching organization with ID:", id);
        const response = await api.get(`/organization/${id}`);
        console.log("Full response:", response);
        console.log("Response data:", response.data);
        
        // Handle different response structures
        let data = null;
        if (response.data?.data) {
          data = response.data.data;
        } else if (response.data) {
          data = response.data;
        }
        
        console.log("Processed data:", data);
        
        if (data) {
          setOrganisasiData(data);
          setError(false);
        } else {
          console.error("No data found in response");
          setError(true);
        }
      } catch (error: any) {
        console.error("Error fetching organisasi data:", error);
        console.error("Error response:", error.response);
        console.error("Error message:", error.message);
        setError(true);
      } finally {
        setLoading(false);
      }
    };

    if (id) {
      fetchOrganisasiData();
    }
  }, [id]);

  if (loading) {
    return (
      <div className="flex justify-center items-center bg-white min-h-screen">
        <div className="border-4 border-gray-200 border-t-primary rounded-full w-16 h-16 animate-spin"></div>
      </div>
    );
  }

  if (error || !organisasiData) {
    return (
      <div className="flex flex-col justify-center items-center bg-white px-6 min-h-screen">
        <h1 className="mb-4 font-bold text-gray-900 text-4xl">Organisasi Tidak Ditemukan</h1>
        <p className="mb-4 text-gray-600">Data organisasi yang Anda cari tidak tersedia.</p>
        <div className="bg-gray-100 mb-6 p-4 rounded-lg">
          <p className="mb-2 font-semibold text-gray-700">Debug Info:</p>
          <p className="text-gray-600 text-sm">ID: {id}</p>
          <p className="text-gray-600 text-sm">Endpoint: /organization/{id}</p>
          <p className="text-gray-600 text-sm">Status: {error ? 'Error' : 'No Data'}</p>
        </div>
        <div className="mb-8 text-center">
          <p className="mb-2 text-gray-600 text-sm font-semibold">Kemungkinan penyebab:</p>
          <ul className="text-gray-500 text-sm list-disc list-inside text-left">
            <li>ID organisasi tidak valid atau tidak ada di database</li>
            <li>API endpoint berbeda (cek console log)</li>
            <li>Koneksi ke server bermasalah</li>
            <li>Response structure tidak sesuai</li>
          </ul>
          <p className="mt-4 text-gray-500 text-xs">Cek browser console (F12) untuk detail error</p>
        </div>
        <button
          onClick={() => router.push('/organisasi')}
          className="flex items-center gap-2 bg-primary hover:bg-primary/90 px-6 py-3 rounded-full font-semibold text-white transition-colors"
        >
          <ArrowLeft className="w-5 h-5" />
          Kembali ke Halaman Organisasi
        </button>
      </div>
    );
  }

  return (
    <main className="bg-white min-h-screen">
      {/* Header Section */}
      <section className="relative bg-secondary py-20 overflow-hidden">
        {/* Decorative elements */}
        <div className="absolute inset-0 opacity-10">
          <div className="top-0 left-1/4 absolute bg-white blur-3xl rounded-full w-96 h-96"></div>
          <div className="right-1/4 bottom-0 absolute bg-white blur-3xl rounded-full w-96 h-96"></div>
        </div>

        <div className="relative mx-auto px-6 max-w-7xl container">
          <motion.div
            initial={{ opacity: 0, y: -30 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.8 }}
          >
            {/* Back Button */}
            <motion.button
              onClick={() => router.push('/organisasi')}
              className="flex items-center gap-2 mb-8 text-white/80 hover:text-white transition-colors"
              initial={{ opacity: 0, x: -20 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ duration: 0.6, delay: 0.2 }}
            >
              <ArrowLeft className="w-5 h-5" />
              Kembali ke Organisasi
            </motion.button>

            {/* Logo and Title */}
            <div className="flex flex-col items-center text-center">
              <motion.div
                className="flex justify-center items-center bg-white shadow-2xl mb-6 rounded-3xl w-32 h-32 overflow-hidden"
                initial={{ opacity: 0, scale: 0.8 }}
                animate={{ opacity: 1, scale: 1 }}
                transition={{ duration: 0.6, delay: 0.3 }}
              >
                <Image
                  src={organisasiData.logo}
                  alt={`Logo ${organisasiData.name}`}
                  width={128}
                  height={128}
                  className="p-4 object-contain"
                />
              </motion.div>

              <motion.h1
                className="mb-6 font-bold text-white text-5xl md:text-6xl"
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.6, delay: 0.4 }}
              >
                {organisasiData.name}
              </motion.h1>

              <motion.p
                className="mx-auto max-w-3xl text-white/90 text-xl leading-relaxed"
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.6, delay: 0.5 }}
              >
                {organisasiData.short_description}
              </motion.p>
            </div>
          </motion.div>
        </div>
      </section>

      {/* Visi Misi Section */}
      <section className="bg-gray-50 py-16">
        <div className="mx-auto px-6 max-w-7xl">
          <div className="gap-8 grid grid-cols-1 lg:grid-cols-2">
            {/* Visi Card */}
            <motion.div
              className="group relative bg-white shadow-lg hover:shadow-2xl p-8 rounded-3xl overflow-hidden transition-all duration-300"
              initial={{ opacity: 0, x: -30 }}
              whileInView={{ opacity: 1, x: 0 }}
              transition={{ duration: 0.6 }}
              viewport={{ once: true }}
            >
              <div className="absolute inset-0 bg-gradient-to-br from-primary/5 to-secondary/5 opacity-0 group-hover:opacity-100 transition-opacity duration-300"></div>
              
              <div className="relative">
                <div className="flex items-center gap-4 mb-6">
                  <div className="flex justify-center items-center bg-gradient-to-br from-primary to-secondary shadow-lg rounded-2xl w-14 h-14">
                    <Eye className="w-7 h-7 text-white" />
                  </div>
                  <h2 className="font-bold text-gray-900 text-3xl">Visi</h2>
                </div>
                <p className="text-gray-700 text-lg leading-relaxed">
                  {organisasiData.visi}
                </p>
              </div>
            </motion.div>

            {/* Misi Card */}
            <motion.div
              className="group relative bg-white shadow-lg hover:shadow-2xl p-8 rounded-3xl overflow-hidden transition-all duration-300"
              initial={{ opacity: 0, x: 30 }}
              whileInView={{ opacity: 1, x: 0 }}
              transition={{ duration: 0.6 }}
              viewport={{ once: true }}
            >
              <div className="absolute inset-0 bg-gradient-to-br from-secondary/5 to-primary/5 opacity-0 group-hover:opacity-100 transition-opacity duration-300"></div>
              
              <div className="relative">
                <div className="flex items-center gap-4 mb-6">
                  <div className="flex justify-center items-center bg-gradient-to-br from-secondary to-primary shadow-lg rounded-2xl w-14 h-14">
                    <CheckCircle className="w-7 h-7 text-white" />
                  </div>
                  <h2 className="font-bold text-gray-900 text-3xl">Misi</h2>
                </div>
                <div className="text-gray-700 text-lg leading-relaxed">
                  {organisasiData.misi}
                </div>
              </div>
            </motion.div>
          </div>
        </div>
      </section>

      {/* Long Description Section */}
      {organisasiData.long_description && (
        <section className="mx-auto px-6 py-16 max-w-7xl">
          <motion.div
            className="mx-auto max-w-4xl"
            initial={{ opacity: 0, y: 30 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6 }}
            viewport={{ once: true }}
          >
            <h2 className="mb-8 font-bold text-gray-900 text-4xl text-center">
              Tentang {organisasiData.name}
            </h2>
            <div className="space-y-4 text-gray-700 text-lg leading-relaxed">
              {organisasiData.long_description.split('\n\n').map((paragraph: string, index: number) => (
                <p key={index}>{paragraph}</p>
              ))}
            </div>
          </motion.div>
        </section>
      )}

      {/* CTA Section */}
      <section className="bg-secondary py-16">
        <div className="mx-auto px-6 max-w-7xl text-center">
          <motion.h2
            className="mb-4 font-bold text-white text-3xl"
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6 }}
            viewport={{ once: true }}
          >
            Tertarik Bergabung?
          </motion.h2>
          <motion.p
            className="mb-8 text-white/90 text-lg"
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6, delay: 0.2 }}
            viewport={{ once: true }}
          >
            Jadilah bagian dari {organisasiData.name} dan kembangkan potensi dirimu
          </motion.p>
          <motion.button
            onClick={() => router.push('/organisasi')}
            className="inline-block bg-white hover:bg-gray-100 shadow-lg px-8 py-4 rounded-full font-bold text-primary text-xl transition-colors"
            initial={{ opacity: 0, scale: 0.9 }}
            whileInView={{ opacity: 1, scale: 1 }}
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            transition={{ duration: 0.6, delay: 0.3 }}
            viewport={{ once: true }}
          >
            Lihat Organisasi Lainnya
          </motion.button>
        </div>
      </section>
    </main>
  );
}